/**
 * @file
 * The modern CSS reset.
 * @see https://github.com/hankchizljaw/modern-css-reset
 */
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box; }

/* Remove default padding */
ul[class],
ol[class] {
  padding: 0; }

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
figure,
blockquote,
dl,
dd {
  margin: 0; }

/* Set core root defaults */
html {
  scroll-behavior: smooth; }

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5; }

/* Remove list styles on ul, ol elements with a class attribute */
ul[class],
ol[class] {
  list-style: none; }

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto; }

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block; }

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em; }

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit; }

/* Blur images when they have no alt attribute */
/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important; } }

:root {
  --color-black: #121212;
  --color-black--light-4: #1b1b1b;
  --color-black--light-7: #212121;
  --color-black--light-12: #2d2d2d;
  --color-black--light-14: #323232;
  --color-black--light-16: #373737;
  --color-black--light-24: #4a4a4a;
  --color-black--opacity-10: rgba(0, 0, 0, 0.1);
  --color-black--opacity-50: rgba(0, 0, 0, 0.5);
  --color-white: #FFFFFF;
  --color-white--dark-1: #FCFCFC;
  --color-cultured: #F9F9F9;
  --color-platinum: #EBEBED;
  --color-light-gray: #D4D4D8;
  --color-gainsboro: #D8D9E0;
  --color-silver-metallic: #ADAEB3;
  --color-manatee: #8E929C;
  --color-dark-silver: #545560;
  --color-roman-silver: #82828C;
  --color-raisin-black: #222330;
  --color-celtic-blue: #0366D6;
  --color-denim: #035FC9;
  --color-blue-crayola: #0470EC;
  --color-fresh-sky-blue: #EBF4FF;
  --color-light-cornflower-blue: #84A9C0;
  --color-light-cornflower-blue--dark-1: #6BBCEF;
  --color-maximum-red: #D72222;
  --color-maximum-red--dark-1: #C91F1F;
  --color-maximum-red--light-1: #DE2E2E;
  --color-maximum-red--light-2: #FCEFEF;
  --color-international-orange-engineering: #BA2D0B;
  --color-orange-yellow-crayola: #FED766;
  --color-capri: #00BBF9;
  --color-jade: #26A769;
  --color-sunglow: #ffd23f;
  --color-jet: #353535;
  --color-border: var(--color-silver-metallic);
  --color-border-secondary: var(--color-platinum);
  --color-disabled: var(--color-platinum); }

[data-theme="dark"] {
  --color-border: var(--color-black--light-24);
  --color-border-secondary: var(--color-black--light-14); }

:root {
  --grid-gap: var(--spacing-md);
  --grid-container-max-width: 100%;
  --grid-template-columns-12: repeat(12, [col] 1fr); }
  @media (--lg) {
    :root {
      --grid-container-max-width: 940px; } }
  @media (--xl) {
    :root {
      --grid-container-max-width: 1280px; } }

:root {
  --icon-theme-light-dark--white: svg-load('theme-light-dark.svg', fill=#FFF);
  --icon-magnify--white: svg-load('magnify.svg', fill=#FFF);
  --icon-drupal--white: svg-load('drupal.svg', fill=#fff);
  --icon-drupal--dark: svg-load('drupal.svg', fill=#333);
  --icon-lightbulb-on-outline: svg-load('lightbulb-on-outline.svg');
  --icon-pencil-outline: svg-load('pencil-outline.svg');
  --icon-alert-rhombus-outline--white: svg-load('alert-rhombus-outline.svg', fill=#FFF);
  --icon-alert-decagram-outline: svg-load('alert-decagram-outline.svg');
  --icon-web--white: svg-load('web.svg', fill=#fff);
  --icon-download-outline--white: svg-load('download-outline.svg', fill=#fff);
  --icon-arrow-left: svg-load('arrow-left.svg');
  --icon-arrow-left--manatee: svg-load('arrow-left.svg', fill=#8E929C);
  --icon-arrow-left--persian-blue: svg-load('arrow-left.svg', fill=#003CC5);
  --icon-arrow-left--light-cornflower-blue--dark-1: svg-load('arrow-left.svg', fill=#6BBCEF);
  --icon-magnify: svg-load('magnify.svg');
  --icon-menu--white: svg-load('menu.svg', fill=#fff);
  --icon-close--white: svg-load('close.svg', fill=#fff);
  --icon-chevron-down: svg-load('chevron-down.svg');
  --icon-chevron-down--white: svg-load('chevron-down.svg', fill=#fff);
  --icon-comment-question-outline--jade: svg-load('comment-question-outline.svg', fill=#26A769);
  --icon-github-white: svg-load('github.svg', fill=#FFF); }

:root {
  --layout-container-side-gap: var(--spacing-sm); }
  @media (--md) {
    :root {
      --layout-container-side-gap: var(--spacing-lg); } }

.layout-container {
  width: 100%;
  max-width: var(--grid-container-max-width);
  margin: 0 auto;
  padding: 0 var(--layout-container-side-gap); }

@custom-media --sm (min-width: 576px);
@custom-media --md (min-width: 768px);
@custom-media --lg (min-width: 992px);
@custom-media --xl (min-width: 1200px);
:root {
  --radius-xxs: 0.125rem;
  /* 0.125 * 16px = 2px */
  --radius-xs: 0.25rem;
  /* 0.25 * 16px = 4px */
  --radius-sm: 0.5rem;
  /* 0.5 * 16px = 8px */
  --radius-md: 0.75rem;
  /* 0.75 * 16px = 12px */
  --radius-lg: 1rem;
  /* 1 * 16px = 16px */ }

:root {
  --shadow-xs: 0px 2px 4px var(--color-black--opacity-10);
  --shadow-sm: 0px 4px 10px var(--color-black--opacity-10);
  --shadow-md: 0px 14px 30px var(--color-black--opacity-10); }

:root {
  --spacing-xxs: 0.25rem;
  /* 0.25 * 16px = 4px */
  --spacing-xs: 0.5rem;
  /* 0.5 * 16px = 8px */
  --spacing-sm: 0.75rem;
  /* 0.75 * 16px = 12px */
  --spacing-md: 1rem;
  /* 1 * 16px = 16px */
  --spacing-lg: 1.5rem;
  /* 1.5 * 16px = 24px */
  --spacing-xl: 3rem;
  /* 3 * 16px = 48px */ }
